import Document, { Head, Main, NextScript } from 'next/document'
import flush from 'styled-jsx/server'
import Nav from '@/components/common/Nav'
import Footer from '@/components/common/Footer'

// import '@/assets/sass/global.scss';
import { isProd, enterPath, staticPath, staticBalePath } from '@/mode-config'
export default class RootDocument extends Document {
  static getInitialProps({ renderPage }) {
    const { html, head, errorHtml, chunks } = renderPage()
    const styles = flush()
    return { html, head, errorHtml, chunks, styles }
  }

  render() {
    const fileVersion = `${
      isProd
        ? // ? this.props.__NEXT_DATA__.buildStats['app.js'].hash
          this.props.__NEXT_DATA__.buildId
        : Math.random()
            .toString(36)
            .slice(2)
    }`
    return (
      <html>
        <Head>
          <meta charset="utf-8" />
          <meta http-equiv="X-UA-Compatible" content="IE=edge" />
          <meta
            name="viewport"
            content="width=device-width, initial-scale=1,maximum-scale=1, user-scalable=no"
          />
          <title>刘关化|个人简历|前端工程师</title>
          <meta
            name="keywords"
            content="刘关化,web前端,前端工程师,简历,web前端个人简历,前端开发,前端简历,前端博客,热前端,设计,FE,github,求职,liuguanhua.github.io,liuguanhua"
          />
          <meta
            name="description"
            content="多年的前端开发经验,奋斗在前端路上的一个技术人员"
          />
          <meta name="author" content="刘关化,前端,简历" />
          <meta name="apple-mobile-web-app-capable" content="yes" />
          <link
            href={`${staticPath}favicon.ico?v=${fileVersion}`}
            rel="icon"
            type="image/x-icon"
          />
          <link
            rel="stylesheet"
            href={`${staticBalePath}style` + `.css?v=${fileVersion}`}
          />{' '}
          {/* <link
            rel="stylesheet"
            href={`${staticPath}/styles/global.min` + '.css?v=' + `${isProd
            ? this.props.__NEXT_DATA__.buildStats['app.js'].hash
            : Math
              .random()
              .toString(36)
              .slice(2)}`}/>
            <base href={`${enterPath}`} /> */}
        </Head>
        <body>
          {this.props.customValue}
          <Main>
            <Footer />
          </Main>
          <NextScript />
        </body>
      </html>
    )
  }
}



